import React from "react";
import ProjectItem from "./ProjectItem";

export default class ProjectList extends React.Component {
  render() {
    const allProjects = this.props.projects.map(function (project, index) {
      return <ProjectItem project={project} key={index} />;
    });

    return (
      <>
        <div class="wrappering">
          <h1>My Project </h1>
          <h2>here is my last projects </h2>
          <div class="cols">{allProjects}</div>
        </div>
      </>
    );
  }
}
